<template>
  <div class="container">
    <h1>LCC to LC Source Parameter Mapping</h1>
    <form>
      <label for="lcc">LCC Parameters</label>
      <textarea v-model="lcc"></textarea>

      <label for="lcn">LC JSON</label>
      <textarea v-model="lcn"></textarea>

      <button v-on:click="convert">Convert to LC</button>
    </form>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      lcc: "",
      lcn: ""
    }
  },
  methods: {
    convert: function(event) {
      let converted_lcc = [];
      event.preventDefault();
      console.log(this.lcc);
      var split = this.lcc.split("&");
      split.forEach( (element) => {
        let fields = element.split("=")
        let value = fields[1].slice(2, -1)
        let property = fields[0]
        if (value !== property) {
          converted_lcc.push(`{\n  "value":"${value}",\n  "property":"{{${fields[0]}}}"\n}`)
        }
      })
      this.lcn = converted_lcc.join(",\n");
    }
  }
}
</script>

<style>

</style>



